@import url(https://fonts.googleapis.com/css?family=Cormorant+Garamond);
.contact {
  background-color: #C8DAB9;
  padding-bottom: 50px; }

/*
  Hover and click on the card for animations!
*/
*,
*::before,
*::after {
  box-sizing: border-box; }

html > .contact-card {
  font-size: 10.5px;
  letter-spacing: 1px;
  text-shadow: 0 0 2px rgba(0, 0, 0, 0.5), -1px -1px 1px rgba(179, 179, 179, 0.5), 1px 1px 0 rgba(255, 255, 255, 0.55), 0 1px 3px white;
  color: #C8DAB9;
  background-color: azure; }

button {
  display: block;
  position: relative;
  background: none;
  color: #F2F8F1;
  border: none;
  padding: 0;
  font: inherit;
  text-transform: inherit;
  letter-spacing: inherit;
  text-shadow: inherit;
  cursor: pointer;
  outline: inherit;
  z-index: 10; }

.no-style {
  text-decoration: none;
  color: #F2F8F1; }

.bizcard-title {
  font-size: 24px;
  font-weight: bold;
  width: 100%; }

.bizcard-punch {
  text-transform: uppercase; }

.bizcard-contact {
  font-size: 12px;
  position: absolute;
  bottom: 0; }

.clearfix::after {
  content: "";
  clear: both;
  display: table; }

.big {
  font-size: 150%; }

.med {
  font-size: 100%;
  letter-spacing: .5px; }

.small {
  font-size: 65%;
  letter-spacing: .5px; }

.no-space {
  letter-spacing: 0px; }

.contact-card {
  background-color: #333230;
  height: 200px;
  width: 350px;
  margin: 0 auto 0 auto;
  padding: 0px 10px 0px 10px;
  transform: rotateX(60deg) rotateY(0deg) rotateZ(45deg);
  /*transform-origin: 50% 100%;*/
  box-shadow: 0;
  transition: transform .4s ease, box-shadow .4s ease; }

.contact-card:hover {
  cursor: pointer;
  transform: rotateX(60deg) rotateY(0deg) rotateZ(45deg) translateZ(10px);
  box-shadow: 20px 20px 20px rgba(0, 0, 0, 0.4); }

.contact-card:focus {
  transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg) translateZ(10px);
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.4); }

.contact-card:focus::before {
  transform: skewX(0deg) translateX(0px) translateY(0px);
  height: 0px; }

.contact-card:focus::after {
  transform: skewY(0deg) translateX(0px) translateY(0px);
  width: 0px; }

.contact-card::before,
.contact-card::after {
  content: "";
  position: absolute;
  display: block;
  background-color: grey;
  transition: transform .4s ease, height .4s ease, width .4s ease; }

.contact-card::before {
  width: 100%;
  height: 6px;
  bottom: 0;
  left: 0;
  transform: skewX(45deg) translateX(-3px) translateY(6px); }

.contact-card::after {
  height: 100%;
  width: 6px;
  top: 0;
  right: 0;
  transform: skewY(45deg) translateX(6px) translateY(-3px); }

.row {
  display: block;
  text-align: center; }

.left {
  float: left; }

.right {
  text-align: right;
  padding-right: 10px; }

p {
  margin: 0; }
