.contact-card {
  background-color: #333230;
  height: 200px;
  width: 350px;
  margin: 0 auto 0 auto;
  padding: 0px 10px 0px 10px;
  -webkit-transform: rotateX(60deg) rotateY(0deg) rotateZ(45deg);
          transform: rotateX(60deg) rotateY(0deg) rotateZ(45deg);
  /*transform-origin: 50% 100%;*/
  box-shadow: 0;
  -webkit-transition: box-shadow .4s ease, -webkit-transform .4s ease;
  transition: box-shadow .4s ease, -webkit-transform .4s ease;
  transition: transform .4s ease, box-shadow .4s ease;
  transition: transform .4s ease, box-shadow .4s ease, -webkit-transform .4s ease; }

.contact-card:hover {
  cursor: pointer;
  -webkit-transform: rotateX(60deg) rotateY(0deg) rotateZ(45deg) translateZ(10px);
          transform: rotateX(60deg) rotateY(0deg) rotateZ(45deg) translateZ(10px);
  box-shadow: 20px 20px 20px rgba(0, 0, 0, 0.4); }

.contact-card:focus {
  -webkit-transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg) translateZ(10px);
          transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg) translateZ(10px);
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.4); }

.contact-card:focus::before {
  -webkit-transform: skewX(0deg) translateX(0px) translateY(0px);
          transform: skewX(0deg) translateX(0px) translateY(0px);
  height: 0px; }

.contact-card:focus::after {
  -webkit-transform: skewY(0deg) translateX(0px) translateY(0px);
          transform: skewY(0deg) translateX(0px) translateY(0px);
  width: 0px; }

.contact-card::before,
.contact-card::after {
  content: "";
  position: absolute;
  display: block;
  background-color: grey;
  -webkit-transition: height .4s ease, width .4s ease, -webkit-transform .4s ease;
  transition: height .4s ease, width .4s ease, -webkit-transform .4s ease;
  transition: transform .4s ease, height .4s ease, width .4s ease;
  transition: transform .4s ease, height .4s ease, width .4s ease, -webkit-transform .4s ease; }

.contact-card::before {
  width: 100%;
  height: 6px;
  bottom: 0;
  left: 0;
  -webkit-transform: skewX(45deg) translateX(-3px) translateY(6px);
          transform: skewX(45deg) translateX(-3px) translateY(6px); }

.contact-card::after {
  height: 100%;
  width: 6px;
  top: 0;
  right: 0;
  -webkit-transform: skewY(45deg) translateX(6px) translateY(-3px);
          transform: skewY(45deg) translateX(6px) translateY(-3px); }

